














.body{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -40%);
            transform: translate(-50%, -40%);
    display: grid;
    grid-template-columns: 1fr 1fr;
}
body{
    font-family: 'Montserrat', sans-serif;
    height: 100%;
    margin: 0;
    line-height: 1.5;
    font-size: 10px;
}
.secondary-color{
    color: #4481eb;
}
.primary-color{
    color: #2c98fb;
}
*{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
p{
    padding: 0;
    margin: 0;
}
.pt{
    padding: 5px;
}
section{
    min-height: calc(600px - 60px);
    width: 350px;
    margin: 20px;
    -webkit-box-shadow: 0 2px 6px 0 #ddd;
            box-shadow: 0 2px 6px 0 #ddd;
}
.navigation{
    background: #4481eb;
    height: 50px;
    padding: 5px;
    color: #fff;
    text-align: center;
}
.navigation i, h3{
    display: inline;
}
.navigation i{
    float: left;
    padding-left: 10px;
    padding-top: 5px;
    font-size: 30px;
}
.navigation h3{
    font-weight: 200; 
    font-size: 25px;
}
.intro-body{
    background: #2c98fb;
    height: 100px;
}
.box1{
    width: 90%;
    -webkit-box-shadow: 0 0 6px 0 #999;
            box-shadow: 0 0 6px 0 #999;
    border-radius: 10px;
    height: 100px;
    background: #fff;
    color: #888;
    margin:-50px auto auto auto;
    padding: 20px 10px 10px 10px;
    display: grid;
    grid-template-columns: repeat(3, 1fr)
}
.box1 button{
    border-radius: 15px;
    height: 30px;
    padding: 2px;
    font-size: 10px;
    border: none;
    color: #fff;
    background: #2c98fb;
}
.box1 button:hover{
    color: #fff;
    background: #4481eb;
}
.info-div .box {
    width: 90%;
    border-radius: 10px;
    height: 120px;
    background: #fff;
    color: #888;
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    -webkit-box-shadow: 0 2px 6px 0 #999;
            box-shadow: 0 2px 6px 0 #999;
}
.info-div .box img{
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    width: 100%;
    height: 100%;
}
.box-details{
    padding: 10px;
    display: grid;
}
.pdt{
    margin: 15px 0;
}
.pdt h3{
    color: #4481eb;
}
.fr{
    float: right;
    color: #2c98fb;
    font-size: 15px;
}
.fr2{
    float: right;
}
.checked {
    color: orange;
}
footer{
    border-top: 3px solid #888;
}
.footer{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 70%;
    margin: auto;
    padding-top: 10px;
}
.tc{
    text-align: center;
    color: #888;
}
/* Sign In Page */
.sigin-in{
    min-height: 600px;
    width: 350px;
    margin: 20px;
    -webkit-box-shadow: 0 2px 6px 0 #ddd;
            box-shadow: 0 2px 6px 0 #ddd;
    display: grid;
    grid-template-rows: 1fr 1fr;
}
.first-div{
    background: #2c98fb;
    height: 100%;
    width: 100%;
    color: #fff;
}
.first-div-cnt{
    position: relative;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}
.first-div i{ /* IE 9 */
    -webkit-transform: rotate(20deg); /* Safari 3-8 */
    transform: rotate(-45deg);
}
.second-div{
   width: 90%;
   margin: -70px auto auto auto;
   background: #fff;
   padding: 10px 20px;
   border-radius: 10px;
}
.input-div{
    border-bottom: 1px solid #888;
    display: grid;
    grid-template-columns: 90% 10%;
    margin: 10px 0;
    color: #888;
}
input{
    border: none;
    outline: none;
    padding-left: 10px;
    padding-bottom: 10px;
}
.sign-up-btn{
    width: 100%;
    height: 40px;
    margin: 5px 0;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-size: 16px;
    font-weight: 400px;
    background: #2c98fb;
    color: #fff;
    letter-spacing: 1px;
    font-family: 'Montserrat', sans-serif;
}
.sign-up-btn:hover{
    background: #4481eb;
}
.sign-up-btn2{
    width: 100%;
    height: 40px;
    margin: 5px 0;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-weight: 400px;
    background: #2851A3;
    color: #fff;
    letter-spacing: 1px;
    font-family: 'Montserrat', sans-serif;
}
.sign-up-btn2:hover{
    background: rgb(32, 55, 100);
}
.f1{
    font-weight: 100 !important;
    font-size: 10px;
    letter-spacing: 1px;
}
.f2{
    font-weight: 400 !important;
    font-size: 16px;
    letter-spacing: 1px;
}
.signup-footer{
    text-align: center;
    margin: 20px 0 10px 0;
}
.sf-t{
    color: #2c98fb;
}
